<template>
  <div >

        <section class="hero is-primary">
          <div class="hero-body">
            <div class="container">
              <h1 class="title">
                {{msg}}
              </h1>
              <h2 class="subtitle is-centered">
                <form @submit.prevent = "login" class="columns is-centered">
                <div class="field has-addons column is-4">
                  <div class="control">
                    <input class="input" v-model="phone" v-on:keyup.enter="login" type="text" placeholder="numarul de telefon" required
                    >
                  </div>
                  <div class="control">
                    <button class="button is-info" click="login">
                      Autentificare
                    </button>
                  </div>
                </div>
                
                </form>                
              </h2>
              <h2  class="subtitle">
                administreaza simplu si rapid consumul de carburant pentru vehiculele tale.
              </h2>
            </div>
          </div>
        </section>
  </div>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  name: 'Welcome',
  props: {
    msg: String   
  },
  data(){
    return{
      phone:''
    }
  },
  methods:{
    ...mapActions(['setPhone']),
    login(){
        this.setPhone(this.phone);
        this.$router.push("dashboard");
      }
  }
}
</script>

